import React from "react";



function Resume(props){
return(
  <section id="resume">


    <div className="row education">

      <div className="three columns header-col">
        <h1><span>Education</span></h1>
      </div>

      <div className=" special nine columns main-col">

        <div className="row item">

          <div className="twelve columns">

            <h3>{props.educationData.courseName}</h3>
            <p className="info1">{props.educationData.courseDescription}<span>&bull;</span> <strong className="date">{props.educationData.courseDate}</strong></p>
            <p className="info">
              <i>  {props.educationData.courseDetails} </i>
            </p>


          </div>

        </div>
        <div className="row item">

          <div className="twelve columns">

            <h3>{props.educationData.universityName}</h3>
            <p className="info1">{props.educationData.studyDescription}<span>&bull;</span> <em className="date">{props.educationData.universityDate}</em></p>



          </div>

        </div>

      </div>

      <hr />
    </div>





    <div className="row skill">

      <div className="three columns header-col">
        <h1><span>Skills</span></h1>
      </div>

      <div className="nine columns main-col">


        <div className="bars">

          <ul className="skills">



            <li><span className="bar-expand css"></span><em>Adobe Premiere Pro</em></li>
            <li><span className="bar-expand html5"></span><em>WIX</em></li>
            <li><span className="bar-expand JavaScript"></span><em>WordPress</em></li>
            <li><span className="bar-expand jquery"></span><em>User experience</em></li>
            <li><span className="bar-expand react"></span><em>Web design</em></li>
            <li><span className="bar-expand java"></span><em>Photoshop</em></li>
            <li><span className="bar-expand node"></span><em>Sony Vegas</em></li>
            <li><span className="bar-expand sony"></span><em>Java</em></li>
            <li><span className="bar-expand photoshop"></span><em>Node.js</em></li>

          </ul>

        </div>

      </div>

    </div>

  </section>


)

}

export default Resume;
