import React from "react";

function About(props){
return(
  <section id="about">

    <div className="row">

      <div className="three columns">

        <img className="profile-pic"  src="images/profilepic.jpg" alt="" />

      </div>

      <div className="special nine columns main-col">

        <h2>About Me</h2>

        <p>{props.aboutMe.aboutMe}
        </p>
        
        <div className="row">

          <div className="columns contact-details">

            <h2>Contact Details</h2>
            <p className="address">
              <span>Andrew Przytula</span><br />

              <span>  Winnipeg, MB Canada </span><br />
              <span>(204)7987092</span><br />
              <span>and.przytula@gmail.com</span>
            </p>

          </div>

          <div className="columns download">
            <p>
              <a href="https://drive.google.com/file/d/1oP3oHQjJ0sIb4hfB3bVFgr12tp-ne188/view?usp=sharing"  className="button"><i className="fa fa-download"></i>Download Resume</a>
            </p>
          </div>

        </div>

      </div>
      <hr />
    </div>


  </section>

)


}

export default About;
