import React from "react"


function Header(props){

  return(
  <header id="home">
    <nav id="nav-wrap">
      <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
      {/* <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a> */}

      <ul id="nav" className="nav">
        <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
        <li><a className="smoothscroll" href="#about">About</a></li>
        <li><a className="smoothscroll" href="#resume">Resume</a></li>
        <li><a className="smoothscroll" href="#portfolio">Works</a></li>
        <li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
        <li><a className="smoothscroll" href="#contact">Contact</a></li>
      </ul>

    </nav>

    <div className="row banner">
      <div className="banner-text">
        <h1  className="banner-text1">{props.resumeData.name}</h1>

        <h3>I'm a Winnipeg based <span>{props.resumeData.role}</span> {props.resumeData.roleDescription}. Let's <a className="smoothscroll" href="#about"> start scrolling </a>
          and learn more <a className="smoothscroll" href="#about">about me</a>.</h3>
        <hr />
        <ul className="social">
          <li><a href="https://linkedin.com/in/andrew-przytula-91475413a"><i className="fa fa-linkedin"></i></a></li>
          <li><a href="https://github.com/AndrewPrzytula"><i className="fa fa-github"></i></a></li>
          <li><a href="skype:live:and.przytula?userinfo"><i className="fa fa-skype"></i></a></li>  
        </ul>
      </div>
    </div>

    <p className="scrolldown">
      <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
    </p>

  </header>

)
}

export default Header;
